  align-items: center;
  display: flex;
}

.linear_btn {
  cursor: pointer;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border: 1px solid #111;
  border-radius: 74px;
  margin-bottom: 20px;
  padding: 12px 24px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16;
}

.linear_btn:hover, .linear_.btn:focus {
  color: #fff;
  background: none;
  border: 1px solid #f87719;
  border-color: #ffc226 #f87719 #f87719 #ffc226;
}

.light_color {
  color: #111;
  background-color: #fff;
}

.light_color:hover, .light_color:focus {
