}

.light_fill {
  fill: #111;
}

.light_shadow {
  box-shadow: 1px 1px 14px 4px rgba(0, 0, 0, .22);
}

.light-theme {
  background-color: #fff;
}

.light-theme p {
  color: #282828;
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 9;
  background: rgba(255, 255, 255, .2);
  transition: opacity .75s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

@media screen and (max-width: 767px) {
